import { componentConfList } from "./../../components/QuestionComponents/index";
import { PayloadAction, createSlice } from "@reduxjs/toolkit";
import { ComponentPropsType } from "../../components/QuestionComponents";
import { produce } from "immer";
import { getNextSelectId, insertNewComponent } from "./utils";
import cloneDeep from "lodash.clonedeep";
import { arrayMove } from "@dnd-kit/sortable";
export type ComponentInfoType = {
  fe_id: string;
  type: string;
  title: string;
  isHidden?: boolean;
  isLocked?: boolean;
  props: ComponentPropsType;
};

export type ComponentsStateType = {
  selectedId: string;
  componentList: Array<ComponentInfoType>;
  copiedComponent: ComponentInfoType | null;
};
const INIT_STATE: ComponentsStateType = {
  selectedId: "",
  componentList: [],
  copiedComponent: null,
};
const componentsSlice = createSlice({
  name: "components",
  initialState: INIT_STATE,
  reducers: {
    resetComponents: (
      state: ComponentsStateType,
      action: PayloadAction<ComponentsStateType>
    ) => {
      return action.payload;
    },
    changeSelectedId: produce(
      (draft: ComponentsStateType, action: PayloadAction<string>) => {
        draft.selectedId = action.payload;
      }
    ),
    addComponent: produce(
      (
        draft: ComponentsStateType,
        action: PayloadAction<ComponentInfoType>
      ) => {
        const newComponent = action.payload;
        insertNewComponent(draft, newComponent);
      }
    ),
    changeComponentProps: produce(
      (
        draft: ComponentsStateType,
        action: PayloadAction<{ fe_id: string; newProps: ComponentPropsType }>
      ) => {
        const { fe_id, newProps } = action.payload;
        const curCom = draft.componentList.find((c) => c.fe_id === fe_id);
        if (curCom) {
          curCom.props = {
            ...curCom.props,
            ...newProps,
          };
        }
      }
    ),
    removeSelectedComponent: produce((draft: ComponentsStateType) => {
      const { componentList = [], selectedId: removeId } = draft;
      const newSelected = getNextSelectId(removeId, componentList);
      draft.selectedId = newSelected;
      const index = componentList.findIndex((c) => c.fe_id === removeId);
      componentList.splice(index, 1);
    }),
    changeComponentHidden: produce(
      (
        draft: ComponentsStateType,
        action: PayloadAction<{ fe_id: string; isHidden: boolean }>
      ) => {
        const { componentList = [] } = draft;
        const { fe_id, isHidden } = action.payload;
        let newSelectId = "";
        if (isHidden) {
          newSelectId = getNextSelectId(fe_id, componentList);
        } else {
          newSelectId = fe_id;
        }
        draft.selectedId = newSelectId;
        const curCom = componentList.find((c) => c.fe_id === fe_id);
        if (curCom) {
          curCom.isHidden = isHidden;
        }
      }
    ),
    toggleComponentLocked: produce(
      (
        draft: ComponentsStateType,
        action: PayloadAction<{ fe_id: string }>
      ) => {
        const { fe_id } = action.payload;
        const curCom = draft.componentList.find((c) => c.fe_id === fe_id);
        if (curCom) {
          curCom.isLocked = !curCom.isLocked;
        }
      }
    ),
    copySelectedComponent: produce((draft: ComponentsStateType) => {
      const { selectedId, componentList } = draft;
      const selectComponent = componentList.find((c) => c.fe_id === selectedId);
      if (selectComponent == null) return;
      draft.copiedComponent = cloneDeep(selectComponent);
    }),
    pasteCopiedComponent: produce((draft: ComponentsStateType) => {
      const { copiedComponent } = draft;
      if (copiedComponent == null) return;
      copiedComponent.fe_id = `${Date.now()}`;
      insertNewComponent(draft, copiedComponent);
    }),
    selectPrevComponent: produce((draft: ComponentsStateType) => {
      const { componentList = [], selectedId } = draft;
      const selectedIndex = componentList.findIndex(
        (c) => c.fe_id === selectedId
      );
      if (selectedIndex < 0) return;
      if (selectedIndex <= 0) return;
      draft.selectedId = componentList[selectedIndex - 1].fe_id;
    }),
    selectNextComponent: produce((draft: ComponentsStateType) => {
      const { componentList = [], selectedId } = draft;
      const selectedIndex = componentList.findIndex(
        (c) => c.fe_id === selectedId
      );
      if (selectedIndex < 0) return;
      if (selectedIndex + 1 === componentList.length) return;
      draft.selectedId = componentList[selectedIndex + 1].fe_id;
    }),
    changeComponentTitle: produce(
      (
        draft: ComponentsStateType,
        action: PayloadAction<{ fe_id: string; title: string }>
      ) => {
        const { fe_id, title } = action.payload;
        const curCom = draft.componentList.find((c) => c.fe_id === fe_id);
        if (curCom) {
          curCom.title = title;
        }
      }
    ),
    moveComponent: produce(
      (
        draft: ComponentsStateType,
        action: PayloadAction<{ oldIndex: number; newIndex: number }>
      ) => {
        const { componentList } = draft;
        const { oldIndex, newIndex } = action.payload;
        draft.componentList = arrayMove(componentList, oldIndex, newIndex);
      }
    ),
  },
});

export const {
  resetComponents,
  changeSelectedId,
  addComponent,
  pasteCopiedComponent,
  copySelectedComponent,
  toggleComponentLocked,
  removeSelectedComponent,
  changeComponentProps,
  selectNextComponent,
  changeComponentHidden,
  selectPrevComponent,
  changeComponentTitle,
  moveComponent,
} = componentsSlice.actions;
export default componentsSlice.reducer;
