import { useDebounce, useDebounceFn, useRequest, useTitle } from "ahooks";
import { Button, Card, Empty, Space, Spin } from "antd";
import "./List.scss";
import { getQuestionListService } from "../../services/quetions";
import { useEffect, useMemo, useRef, useState } from "react";
import { useSearchParams } from "react-router-dom";
import { QuestionCard } from "../../components/QuestionCard";
const List = () => {
  useTitle("我的问卷");
  // const [list, setList] = useState([]);
  // const [total, setTotal] = useState(0);
  // const loading = false;
  // useEffect(() => {
  //   async function load() {
  //     const data = await getQuestionListService();
  //     const { list = [], total = 0 } = data.data;
  //     setList(list);
  //     setTotal(total);
  //   }
  //   load();
  // }, []);
  // const { data = {}, loading } = useLoadQuestionListData();
  // const { list = [], total = 0 } = data;
  // console.log(list);
  const [page, setPage] = useState(1);
  const [list, setList] = useState([]);
  const [total, setTotal] = useState(0);
  const [started, setStarted] = useState(false);
  const haveMoreData = total > list.length;
  const [searchParams] = useSearchParams();
  const containerRef = useRef<HTMLDivElement>(null);
  const keyword = searchParams.get("keyword") || "";
  useEffect(() => {
    setStarted(false);
    setPage(1);
    setList([]);
    setTotal(0);
  }, [keyword]);
  const { run: load, loading } = useRequest(
    async () => {
      const data = await getQuestionListService({
        page,
        pageSize: 10,
        keyword,
      });
      return data;
    },
    {
      manual: true,
      onSuccess(result) {
        const { list: l = [], total = 0 } = result.data;
        setList(list.concat(l));
        setTotal(total);
        setPage(page + 1);
      },
    }
  );
  const { run: tryLoadMore } = useDebounceFn(
    () => {
      const elem = containerRef.current;
      if (elem == null) {
        return;
      }
      const domReact = elem.getBoundingClientRect();
      if (domReact == null) {
        return;
      }
      const { bottom } = domReact;
      if (bottom < document.documentElement.clientHeight) {
        load();
        setStarted(() => true);
      }
    },
    {
      wait: 500,
    }
  );

  useEffect(() => {
    tryLoadMore();
  }, [searchParams]);

  useEffect(() => {
    if (haveMoreData) {
      window.addEventListener("scroll", tryLoadMore);
    }
    return () => {
      window.removeEventListener("scroll", tryLoadMore);
    };
  }, [searchParams, haveMoreData]);

  const LoadMoreContentElem = useMemo(() => {
    if (!started || loading) return <Spin />;
    if (total === 0) return <Empty description="暂无数据" />;
    if (!haveMoreData) return <span>没有更多了....</span>;
    return <span>加载下一页...</span>;
  }, [started, loading, haveMoreData]);
  return (
    <div className="manage_list">
      {
        <Space direction="vertical" size="middle" style={{ display: "flex" }}>
          {list.map((item: any, index: number) => (
            <QuestionCard item={item} key={index}></QuestionCard>
          ))}
          <div ref={containerRef}>{LoadMoreContentElem}</div>
        </Space>
      }
    </div>
  );
};
export default List;
