import { Outlet, useLocation, useNavigate } from "react-router-dom";
import "./ManageLayout.scss";
import { Button, message } from "antd";
import {
  DeleteOutlined,
  StarOutlined,
  UnorderedListOutlined,
} from "@ant-design/icons";
import { useState } from "react";
import Title from "antd/es/typography/Title";
import Search, { SearchProps } from "antd/es/input/Search";
import { createQuestionService } from "../services/quetions";
import { useRequest } from "ahooks";

const ManageLayout = () => {
  // const [loading, setLoading] = useState(false);
  const navigate = useNavigate();
  const { pathname } = useLocation();
  const [isSelect, setSelect] = useState(0);

  const handleLiClick = (value: number, url: string) => {
    setSelect(() => value);
    navigate(url);
  };
  const onSearch: SearchProps["onSearch"] = (value, _e, info) => {
    navigate({
      pathname,
      search: `keyword=${value}`,
    });
  };

  // async function createQuestion() {
  //   setLoading(() => true);
  //   const data = await createQuestionService();
  //   const {
  //     data: { id },
  //   } = data || {};
  //   if (id) {
  //     navigate(`/question/edit/${id}`);
  //     message.success("创建成功");
  //   }
  //   setLoading(() => false);
  // }
  const {
    loading,
    // error,
    run: createQuestion,
  } = useRequest(createQuestionService, {
    manual: true,
    onSuccess(result) {
      navigate(`/question/edit/${result.id}`);
      message.success("创建成功");
    },
  });
  return (
    <div className="manageLayout_container">
      <div className="nav">
        <Button type="primary" onClick={createQuestion} disabled={loading}>
          新建问卷
        </Button>
        <ul>
          <li
            className={isSelect === 0 ? "selected" : ""}
            onClick={() => {
              handleLiClick(0, "list");
            }}
          >
            <UnorderedListOutlined />
            <span className="nav_text">我的问卷</span>
          </li>
          <li
            className={isSelect === 1 ? "selected" : ""}
            onClick={() => {
              handleLiClick(1, "star");
            }}
          >
            <StarOutlined />
            <span className="nav_text">标星问卷</span>
          </li>
          <li
            className={isSelect === 2 ? "selected" : ""}
            onClick={() => {
              handleLiClick(2, "trash");
            }}
          >
            <DeleteOutlined />
            <span className="nav_text">回收站</span>
          </li>
        </ul>
      </div>
      <div className="content">
        <div className="content_header">
          <Title level={3}>我的问卷</Title>
          <div className="input_box">
            <Search placeholder="输入关键字" onSearch={onSearch} allowClear />
          </div>
        </div>
        {<Outlet></Outlet>}
      </div>
    </div>
  );
};

export default ManageLayout;
