import {
  CopyOutlined,
  DeleteOutlined,
  EditOutlined,
  LineChartOutlined,
  StarOutlined,
} from "@ant-design/icons";
import { useRequest } from "ahooks";
import { Button, Card, Popconfirm, message } from "antd";
import { useState } from "react";
import {
  duplicateQuestionService,
  updateQuestionService,
} from "../services/quetions";
import { useNavigate } from "react-router-dom";
interface quetionCardProps {
  item: any;
}
export const QuestionCard = (props: quetionCardProps) => {
  const { item } = props;
  const { isStar, _id } = item;
  const navigate = useNavigate();
  const [starState, setStarState] = useState(isStar);
  const { loading: changeStarLoading, run: changeStar } = useRequest(
    async () => {
      await updateQuestionService(_id, { isStar: !isStar });
    },
    {
      manual: true,
      onSuccess() {
        setStarState(!starState);
        message.success("修改成功");
      },
    }
  );
  const renderTitleRight = (item: any) => {
    return (
      <div className="TitleRight">
        {item.isPublished ? (
          <Button type="primary" ghost>
            已发布
          </Button>
        ) : (
          <Button>未发布</Button>
        )}
        <span>答卷:{item.answerCount}</span>
        <span>{item.createAt}</span>
      </div>
    );
  };
  // const duplicate = () => { };
  const { loading: duplicateLaoding, run: duplicate } = useRequest(
    async () => await duplicateQuestionService(_id),
    {
      manual: true,
      onSuccess(result: any) {
        message.success("修改成功");
        navigate(`/question/edit/${result.data.id}`);
      },
    }
  );
  // const del = () => { };
  const [isDeleted, setIsDeleted] = useState(false);
  const { loading: deleteLoading, run: deleteQuestion } = useRequest(
    async () => await updateQuestionService(_id, { isDeleted: true }),
    {
      manual: true,
      onSuccess() {
        message.success("删除成功");
        setIsDeleted(true);
      },
    }
  );
  if (isDeleted) {
    return null;
  }
  function goEdit() {
    navigate(`/question/edit/${item._id}`);
  }
  function goStat() {
    navigate(`/question/stat/${item._id}`);
  }
  return (
    <Card
      title={item.title}
      size="small"
      extra={renderTitleRight(item)}
      key={item._id}
    >
      <div className="card_content">
        <div className="content_left">
          <Button type="text" onClick={goEdit}>
            <EditOutlined />
            编辑问卷
          </Button>
          <Button type="text" onClick={goStat}>
            <LineChartOutlined />
            数据统计
          </Button>
        </div>
        <div className="content_right">
          <Button onClick={changeStar} disabled={changeStarLoading} type="text">
            <StarOutlined />
            {starState ? "取消标星" : "标星"}
          </Button>
          <Popconfirm
            description="确定复制这个问卷吗?"
            onConfirm={duplicate}
            okText="确定"
            cancelText="取消"
            title={"提示"}
          >
            <Button type="text">
              <CopyOutlined />
              复制
            </Button>
          </Popconfirm>

          <Popconfirm
            description="确定复制这个问卷吗?"
            onConfirm={deleteQuestion}
            disabled={deleteLoading}
            okText="确定"
            cancelText="取消"
            title={"提示"}
          >
            <Button>
              <DeleteOutlined />
              删除
            </Button>
          </Popconfirm>
        </div>
      </div>
    </Card>
  );
};
