const { Random } = require("mockjs");
const getComponentList = require("../data/getComponentList");
const { forEach } = require("../mock/question");

module.exports = function getStatList(len = 10) {
  const componentList = getComponentList();
  const res = [];
  for (let i = 0; i < len; i++) {
    const stat = {
      _id: Random.id(),
    };
    componentList.forEach((c) => {
      const { fe_id, type, props } = c;
      switch (type) {
        case "QuestionInput":
          stat[fe_id] = Random.ctitle();
          break;
        case "QuestionTextarea":
          stat[fe_id] = Random.ctitle();
          break;
        case "QuestionRadio":
          stat[fe_id] = props.options[0].text;
          break;
        case "QuestionCheckbox":
          stat[fe_id] = `${props.list[0].text},${props.list[1].text}`;
          break;
      }
    });
    res.push(stat);
  }

  return res;
};
