import { useRequest, useTitle } from "ahooks";
import React, { useState } from "react";
import { Button, Spin, Table, message } from "antd";
import type { TableColumnsType } from "antd";
import "./Trash.scss";
import { useLoadQuestionListData } from "../../hooks/useLoadQuestionListData";
import ListPage from "../../components/ListPage";
import {
  deleteQuestionService,
  updateQuestionService,
} from "../../services/quetions";
interface DataType {
  key: React.Key;
  name: string;
  age: number;
  address: string;
}

const columns: TableColumnsType<DataType> = [
  {
    title: "标题",
    dataIndex: "title",
  },
  {
    title: "是否发布",
    dataIndex: "isPublished",
  },
  {
    title: "答卷数量",
    dataIndex: "answerCount",
  },
  {
    title: "创建时间",
    dataIndex: "createAt",
  },
];

const Trash = () => {
  useTitle("回收站");
  const [selectedRowKeys, setSelectedRowKeys] = useState<React.Key[]>([]);
  const {
    data = {},
    loading,
    refresh,
  } = useLoadQuestionListData({ isDeleted: true });
  const { list = [], total = 0 } = data;
  const onSelectChange = (newSelectedRowKeys: React.Key[]) => {
    console.log("selectedRowKeys changed: ", newSelectedRowKeys);
    setSelectedRowKeys(newSelectedRowKeys);
  };

  const rowSelection = {
    selectedRowKeys,
    onChange: onSelectChange,
  };
  const { run: recover } = useRequest(
    async () => {
      for await (const id of selectedRowKeys) {
        await updateQuestionService(id.toString(), { isDeleted: false });
      }
    },
    {
      manual: true,
      debounceWait: 500, //防抖
      onSuccess() {
        message.success("恢复成功");
        refresh(); //手动刷新列表
        setSelectedRowKeys([]);
      },
    }
  );

  const { run: deleteQuestion } = useRequest(
    async () => {
      for await (const id of selectedRowKeys) {
        await deleteQuestionService(selectedRowKeys as string[]);
      }
    },
    {
      manual: true,
      debounceWait: 500, //防抖
      onSuccess() {
        message.success("删除成功");
        refresh(); //手动刷新列表
        setSelectedRowKeys([]);
      },
    }
  );

  return (
    <div className="trash_box">
      <div className="btnBox">
        <Button
          type="primary"
          disabled={selectedRowKeys.length === 0}
          onClick={recover}
        >
          恢复
        </Button>
        <Button
          type="primary"
          danger
          disabled={selectedRowKeys.length === 0}
          onClick={deleteQuestion}
        >
          彻底删除
        </Button>
      </div>
      {loading && (
        <div className="spinStyle">
          <Spin></Spin>
        </div>
      )}
      {!loading && (
        <>
          <Table
            rowSelection={rowSelection}
            columns={columns}
            dataSource={list}
          />
          <ListPage total={total}></ListPage>
        </>
      )}
    </div>
  );
};
export default Trash;
