import Mock, { Random } from "mockjs";
import getComponentList from "./data/getComponentList";
import getQuestionList from "./data/getQuestionList";
import getStatList from "./data/getStatList";

Mock.mock("/api/test", "get", () => {
  return {
    code: 200,
    data: {
      name: "mockjs",
    },
  };
});

Mock.mock("/api/user/info", "get", () => {
  return {
    errno: 0,
    data: {
      username: Random.title(),
      nickname: Random.cname(),
    },
  };
});
Mock.mock("/api/user/login", "post", () => {
  return {
    errno: 0,
    data: {
      token: Random.word(20),
    },
  };
});

Mock.mock("/api/question/list", "get", (ctx) => {
  const { url = "", query = {} } = ctx as any;
  const isDeleted = url.indexOf("isDeleted=true") >= 0;
  const isStar = url.indexOf("isStar=true") >= 0;
  const pageSize = parseInt(query.pageSize) || 10;
  return {
    errno: 0,
    data: {
      list: getQuestionList({ len: pageSize, isDeleted, isStar }),
      total: 100,
    },
  };
});

Mock.mock(RegExp("/api/question" + ".*"), "get", () => {
  return {
    errno: 0,
    data: {
      id: Random.id(),
      title: Random.ctitle(),
      desc: "问卷描述",
      js: "",
      css: "",
      isPublished: true,
      componentList: getComponentList(),
    },
  };
});

Mock.mock("/api/question", "post", () => {
  return {
    errno: 0,
    data: {
      id: Random.id(),
    },
  };
});

